#root{
    width: 100%;
}

body{
    background: black;
    color: white;
    margin: 0;
}

.App{
    display: grid;
    width: 100%;
    grid-template-rows: 1.5em auto;
    grid-template-rows: max-content;
    grid-template-areas:
        "top-bar"
        "main"
}

#top-bar{
    grid-area: top-bar;
    width: 100%;
    font-size: 1rem;
    display: grid;
    grid-template-columns: auto max-content max-content max-content;
    grid-template-areas:
        "app-title timeline suggest login-logout"
}

#top-bar button{
    font-size: 1rem;
    background-color: transparent;
    border-style: none;
}

@media(max-width:400px){
    #top-bar, #top-bar button{
        font-size: 0.8rem;
    }
}

#top-bar button:focus{
    outline: 0;
    text-decoration: underline;
}

#top-bar .app-title{
    grid-area: app-title;
    color: black;
    background-color: springgreen;
}

#top-bar > .app-title > div{
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
}

#top-bar .timeline{
    grid-area: timeline;
    background-color: GreenYellow;
}

#top-bar .suggest{
    grid-area: suggest;
    background-color: violet;
}

#top-bar .login-logout{
    grid-area: login-logout;
    background-color: yellow;

}

#main{
    grid-area: main;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

.page-title{
    text-align: center;
    margin: 10px;
}

.welcome{
    color: black;
    margin: 5px;
}