.suggestion-label{
    display: inline-block;
}

.suggest-form-element, .suggestion-label{
    width: 400px;
}

@media(max-width:400px){
    .suggest-form-element, .suggestion-label{
        width: 95vw;
    }
}

.suggest-form-element{
    background-color: transparent;
    border-color: springgreen;
    border-style: dotted;
    border-width: 1px;
    color: white;
}

textarea.suggest-form-element{
    resize: none;
}