a.name {
    position:relative;
    color: white;
    text-decoration: none;
}

a.name::before{
    content: "";
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 0.1em;
    bottom: 0;
    background-color: SpringGreen;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
}

a.name:hover::before {
    color:black;
    transform: scaleX(1);
}

.timeline{
    color:springgreen
}

input[type='submit']{
    border-style: none;
    float: right;
    background-color: transparent;
    color: black;
    background-color: springgreen;
    border-radius: 3px;
}

input[type='submit']:hover{
    background-color: violet;
}

button.delete-button{
    border-style: solid;
    border-width: 1px;
    border-color: black;
    float: right;
    background-color: transparent;
    color: white;
    margin-left:15px;
}
button.delete-button:hover{
    color: yellow;
}

.new-person input[type='text']{
    background-color: transparent;
    border-style: none;
    border-bottom-style: dotted;
    border-color: springgreen;
    color:springgreen;
    margin-right: 10px;
}

.new-person input[name='name']{
    color: white;
    border-color: white;
}

